* {
	margin: 0px;
	padding: 0px;
	box-sizing: border-box;
	font-family: Ubuntu;
}
ul,
ol,
li {
	list-style: none;
}
a {
	text-decoration: none;
}
fieldset {
	border: none;
}
table {
	border-collapse: collapse;
}

